<template>
    <div>
        AAAA
    </div>
</template>

<script>
import rolling from '@/common/es/api_bonus/doing/_rolling_tree'
import computed from '@/common/es/api_bonus/doing/_computed'
import vip from '@/common/es/api_bonus/doing/_vip'
import bonus from '@/common/es/api_bonus/doing/_bonus'

    export default {
        name: '',
        data() {
            return {
                
            }
        },
        mounted() {
            this.building()

        },
        methods: {
            rebuild_user(user) {
                delete user.created_at
                delete user.updated_at
                delete user.role
                delete user.provider
                delete user.confirmed
                delete user.blocked
                delete user.email
            },

            async building() {
                let res_sponser, res_enroller

                let users = await this.userLoading()
                let buys = await this.buyLoading()

                users.map(user => {
                    let res = []
                    buys.map(buy => { if (user.id == buy.users_permissions_user) { res.push(buy) } })
                    user.buys = res
                    return user
                })

                if (users) {

                    // 建立树状图
                    res_sponser = await rolling(users, 'SPONSER', this.rebuild_user)
                    res_enroller = await rolling(users, 'ENROLLER', this.rebuild_user)

                    // 获取 中间数组， 获取 VIP 需要的参数
                    let transfer = [ ]
                    transfer = await computed(res_sponser, 'SPONSER', transfer, null)
                    transfer = await computed(res_enroller, 'ENROLLER', transfer, null)
 
                    // 拿到 VIP
                    transfer.map(e => {

                        e.vip = vip(e)
                        return e
                    })

                    // 计算 各种 奖金

                    bonus(res_sponser, transfer, null)
                    console.log(res_sponser)
                }
            },

            async buyLoading() {
                const res = await this.conn.get(
                    this.api.buys,
                    this.$store.state.token
                )

                return res.map(e => {
                        return {
                            num: e.num,
                            id: e.id,
                            pv: e.product.pv,
                            users_permissions_user: e.users_permissions_user.id,
                            buy_time: e.buy_time,
                            product: e.product.id,
                        }
                    })
            },
            async userLoading() {
                const res = await this.conn.get(
                    this.api.users,
                    this.$store.state.token
                )

                return res
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


